.card {
  height: 10vw;
  width: 15vw;
  border-radius: 10px;
  background: linear-gradient(0deg, rgba(255,117,31, 0.5), rgba(255,117,31, 0.5)),
    url(/LLM_WikiMusic/static/media/genreBg.dbc546a6.jpg);
  background-size: 100% 100%;
  background-position: cover;
  box-shadow: 5px 5px 5px black;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2vw;
  text-transform: uppercase;
  color: white;
  text-shadow: 2px 2px 2px black;
  font-weight: bold;
  transition: transform 0.3s;
  font-size: 1.2rem;
}

.card h4 {
  font-size: 1.6rem;
  padding: 0 1rem;
  font-family: 'Permanent Marker', cursive;
}

.card:hover {
  transform: scale(1.05);
  cursor: pointer;
}

.links {
  text-decoration: none;
}

@media screen and (max-width: 450px) {
