    background: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.2),
      rgba(0, 0, 0, 0.2)
    ) !important;
    width: 100% !important;
  }
}

@media screen and (min-width: 450px) and (max-width: 768px) {
  .card {
    font-size: 0.7rem !important;
  }
}

