.tracks-details {
  display: hidden;
  width: 0;
}

.tracks-details-is-hidden {
  display: block;
  width: 45vw;
  animation: opacity 1s;
  animation-iteration-count: 1;
  animation-direction: alternate; /* or: normal */
  animation-timing-function: ease-out; /* or: ease, ease-in, ease-in-out, linear, cubic-bezier(x1, y1, x2, y2) */
  animation-fill-mode: forwards; /* or: backwards, both, none */
  animation-delay: 0s;
}

@keyframes opacity {
  0%    { opacity: 0; }
  20%   { opacity: 0.2; }
  40%   { opacity: 0.4; }
  60%   { opacity: 0.6; }
  80%   { opacity: 0.8; }
  100%  { opacity: 1; }
}

@keyframes appear {
  from {
    transform: scale(0);
    opacity: 0;
    display: hidden;
  }
  to {
    transform: scale(1);
    opacity: 1;
    display: block;
    position: fixed;
    height: 100vh;
    top: 0;
    right: 0;
  }
}

